.rock-slider {
  display: block;
  position: relative;
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
  transform-style: preserve-3d;
  overflow: hidden;
}

.rock-slider:after {
  content: "";
  display: table;
  clear: both;
}

.rock-slider.rock-loaded {
  overflow: auto;
}

.rock-slider.rock-dragging {
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
  cursor: move;
}

.rock-slider .rock-slide {
  visibility: 'hidden';
}

.rock-slider .rock-outer {
  display: block;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-outer:after {
  content: "";
  display: table;
  clear: both;
}

.rock-slider .rock-inner {
  -webkit-perspective: 1000px;
  -moz-perspective: 1000px;
  -ms-perspective: 1000px;
  -o-perspective: 1000px;
  perspective: 1000px;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.rock-slider .rock-inner .rock-slide {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
  perspective: 1000;
}

.rock-slider .rock-inner .rock-slide.active {
  display: block;
  z-index: 2;
}

.rock-slider .rock-inner .rock-slide.previous {
  display: block;
  z-index: 1;
}

.rock-slider .rock-inner .rock-slide .rock-layer-wrapper {
  display: block;
  position: absolute;
  width: 100%;
  height: auto;
  opacity: 0;
}

.rock-slider .rock-inner .rock-slide .rock-layer-wrapper .rock-layer {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-inner .rock-slide .rock-object.object-absolute {
  position: absolute;
  width: 100%;
}

.rock-slider .rock-inner .rock-slide .rock-content-wrapper {
  display: block;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  position: absolute;
  z-index: 999;
}

.rock-slider .rock-inner .rock-slide .rock-content {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: table;
}

.rock-slider .rock-inner .rock-slide .rock-content.rock-content-center > .rock-content-container {
  display: table-cell;
  vertical-align: middle;
}

.rock-slider .rock-inner .rock-slide .rock-content.rock-content-top > .rock-content-container {
  display: table-cell;
  vertical-align: top;
}

.rock-slider .rock-inner .rock-slide .rock-content.rock-content-bottom > .rock-content-container {
  display: table-cell;
  vertical-align: bottom;
}

.rock-slider .rock-inner .rock-slide .rock-canvas {
  display: block;
  position: absolute;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-inner .rock-slide .rock-background-wrapper {
  display: block;
  position: absolute;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-inner .rock-slide .rock-background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
}

.rock-slider .rock-inner .rock-slide .rock-video-background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  background: black;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.rock-slider .rock-inner .rock-slide .rock-video {
  width: 100% !important;
  height: 100% !important;
}

.rock-slider .rock-inner .rock-slide .rock-video-cover {
  display: block;
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-inner .rock-slide iframe {
  border: none;
}

.rock-slider .rock-inner .rock-slide .rock-grid {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-inner .rock-slide .rock-grid .rock-grid-cell {
  display: block;
  position: absolute;
  margin: 0 !important;
  padding: 0 !important;
  opacity: 0;
  overflow: hidden;
}

.rock-slider .rock-inner .rock-slide .rock-grid .rock-grid-cell .rock-background {
  position: absolute;
  left: 0;
  top: 0;
}

.rock-slider .rock-grid-slide {
  opacity: 1 !important;
}

.rock-slider .rock-grid-slide .rock-background-wrapper .rock-background-main {
  width: 100%;
  height: 100%;
  opacity: 0;
}

.rock-slider-with-thumbnails {
  position: relative;
  display: block;
  width: 100%;
}

.rock-slider-with-thumbnails.left .rock-thumbnails-container {
  left: 0;
