.rock-slider-with-thumbnails .rock-slider-container {
  display: block;
  position: relative;
}

.rock-slider .rock-loader-wrapper {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
}

.rock-slider .rock-loader-wrapper .rock-loader-text {
  display: block;
  position: absolute;
  top: 50%;
  margin-top: 20px;
  text-align: center;
  width: 100%;
}

.rock-slider .rock-loader-wrapper .rock-loader-text .rock-loader-title {
  margin-bottom: 0;
}

.rock-slider .rock-loader-wrapper .rock-loader-text .rock-loader-subtitle {
  margin-bottom: 0;
}

.rock-slider .rock-loader-wrapper .rock-loader {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 9999;
}

.rock-slider .rock-loader-wrapper .rock-loader .rock-loader-inner {
  position: relative;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-loader-wrapper .rock-loader .rock-loader-inner .rock-loader-tile {
  display: block;
  position: relative;
  padding: 0;
  margin: 0;
  float: left;
  width: 20%;
}

.rock-slider .rock-progress {
  display: block;
  position: absolute;
  left: 0;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 10px;
  z-index: 3;
}

.rock-slider .rock-progress.rock-progress-top {
  top: 0;
}

.rock-slider .rock-progress.rock-progress-bottom {
  bottom: 0;
}

.rock-slider .rock-progress .rock-progress-bar {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 0px;
  height: 10px;
}

.rock-slider .rock-control {
  display: block;
  position: absolute;
  top: 50%;
  z-index: 3;
}

.rock-slider .rock-control.rock-next {
  right: 0;
}

.rock-slider .rock-control.rock-next .rock-control-text {
  float: right;
}

.rock-slider .rock-control.rock-next .rock-control-thumbnail {
  -ms-filter: "progid:DXImageTransform@mixin Microsoft@mixin Alpha(Opacity=${iefactor})";
  filter: "alpha(opacity=(${iefactor}))";
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
  -webkit-transform: translateX(40px);
  -moz-transform: translateX(40px);
  -ms-transform: translateX(40px);
  -o-transform: translateX(40px);
  transform: translateX(40px);
}

.rock-slider .rock-control.rock-next:hover .rock-control-thumbnail {
  -ms-filter: "progid:DXImageTransform@mixin Microsoft@mixin Alpha(Opacity=${iefactor})";
  filter: "alpha(opacity=(${iefactor}))";
  -moz-opacity: 1;
  -khtml-opacity: 1;
  opacity: 1;
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
}

.rock-slider .rock-control.rock-prev {
  left: 0;
}

.rock-slider .rock-control.rock-prev .rock-control-text {
  float: left;
}

.rock-slider .rock-control.rock-prev .rock-control-thumbnail {
  -ms-filter: "progid:DXImageTransform@mixin Microsoft@mixin Alpha(Opacity=${iefactor})";
  filter: "alpha(opacity=(${iefactor}))";
  -moz-opacity: 0;
  -khtml-opacity: 0;
  opacity: 0;
  -webkit-transform: translateX(-100%);
  -moz-transform: translateX(-100%);
  -ms-transform: translateX(-100%);
  -o-transform: translateX(-100%);
  transform: translateX(-100%);
}

.rock-slider .rock-control.rock-prev:hover .rock-control-thumbnail {
  -ms-filter: "progid:DXImageTransform@mixin Microsoft@mixin Alpha(Opacity=${iefactor})";
  filter: "alpha(opacity=(${iefactor}))";
  -moz-opacity: 1;
  -khtml-opacity: 1;
  opacity: 1;
  -webkit-transform: translateX(0px);
  -moz-transform: translateX(0px);
  -ms-transform: translateX(0px);
  -o-transform: translateX(0px);
  transform: translateX(0px);
}

.rock-slider .rock-control .rock-control-inner {
  display: block;
  position: relative;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

.rock-slider .rock-control .rock-control-inner .rock-control-text {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
  display: block;
  position: relative;
  font-weight: bold;
  line-height: 80px;
  text-align: center;
  width: 40px;
  height: 80px;
  z-index: 5;
}

.rock-slider .rock-control .rock-control-inner .rock-control-text:hover {
  text-decoration: none;
}

.rock-slider .rock-control .rock-control-inner .rock-control-thumbnail {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
  display: block;
  position: absolute;
  height: 100%;
}

.rock-slider .rock-control .rock-control-inner .rock-control-thumbnail .rock-control-image {
  display: block;
  width: auto;
  height: 100%;
}

.rock-pagination {
  display: block;
  text-align: center;
  width: 100%;
  height: auto;
  z-index: 3;
}

.rock-pagination.rock-pagination-inside {
  position: absolute;
  bottom: 50px;
  left: 0;
  height: 20px;
}

.rock-pagination.rock-pagination-outside {
  display: block;
  position: relative;
  margin: 25px auto;
}

.rock-pagination .rock-pagination-bullet {
  -webkit-transition: all 0.5s ease;
  -moz-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  -ms-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-border-radius: 100%;
  -moz-border-radius: 100%;
  border-radius: 100%;
  background-clip: padding-box;
  display: inline-block;
  position: relative;
  background: rgba(255, 255, 255, 0.5);
  cursor: pointer;
  margin: 10px;
  width: 10px;
  height: 10px;
  z-index: 999;
}

.rock-pagination .rock-pagination-bullet.active {
  -webkit-transform: scale(1.5);
  -moz-transform: scale(1.5);
  -ms-transform: scale(1.5);
  -o-transform: scale(1.5);
  transform: scale(1.5);
  background: white;
}

.rock-thumbnails {
  position: relative;
  display: block;
  margin: 0 0 25px;
  overflow: hidden;
}

.rock-thumbnails.horizontal {
  width: 100%;
  height: auto;
}

.rock-thumbnails.horizontal .rock-thumbnail {
  width: 100%;
  height: auto;
}

.rock-thumbnails.vertical {
  width: 100%;
  height: 100%;
}

.rock-thumbnails.vertical .rock-thumbnail-wrapper {
  display: block;
  position: relative;
}

.rock-thumbnails.vertical .rock-thumbnail {
  width: auto;
  height: 100%;
}

.rock-thumbnails-inner {
  position: relative;
  display: block;
  padding: 0;
  margin: 0;
  text-align: center;
}

.rock-thumbnails-inner.animating {
  transition: transform 0.5s ease;
}

.rock-thumbnails-inner .rock-thumbnail-wrapper {
  height: auto;
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.rock-thumbnails-inner .rock-thumbnail-wrapper .rock-thumbnail {
  position: relative;
  margin: 0;
  padding: 0;
  display: block;
}
